<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	props: ['bid'],
	setup() {
		return {}
	},
	methods: {
		changeBid() {
			this.$emit(
				'changeBid',
				this.bid
			)
		},
		cancelBid() {
			this.$emit(
				'cancelBid',
				this.bid
			)
		},
		getClass() {
			let result = this.bid.type === 'sell' ? 'sell' : 'buy'
			if (this.profile?.id === this.bid.clientId) {
				result = result + '-mine'
			}
			if (this.bid.isPriority) {
				result = result + ' priority'
			}
			return result
		},
		onPlaceClick() {
			this.$emit(
				'showBidPlace',
				this.bid
			)
		}
	},
	computed: {
		profile() {
			return this.$store.getters.profile
		},
		amount() {
			return (this.bid.amount || 0) / 1000000
		},
		payAmount() {
			return (this.bid.payAmount || 0) / 1000000
		},
		place() {
			return this.bid.place ? this.bid.place : null
		}
	},
	mounted() {
		console.log(this.bid);
	}
})
</script>

